.pb-sidebar {
	/* Overlap at phone width, with menu-button to open and close */
	height: 100%;
	display: -ms-flexbox;
	display: flex;
	padding: 20px;
	width: 100%;
}

.pb-sidebar-label {
	color: black;
}

.pb-sidebar-items {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	width: 100%;
}

.loading:after {
	overflow: hidden;
	display: inline-block;
	vertical-align: bottom;    
	-webkit-animation: ellipsis steps(4, end) 900ms infinite;    
	        animation: ellipsis steps(4, end) 900ms infinite;
	content: "\2026"; /* ascii code for the ellipsis character */
	width: 0px;
}

@-webkit-keyframes ellipsis {
	to {
		width: 1.25em;    
	}
}

@keyframes ellipsis {
	to {
		width: 1.25em;    
	}
}

form {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: start;
	    align-items: flex-start;
	padding: 30px;
	-ms-flex: 1 1 0%;
	    flex: 1 1 0%;
}

form input:hover {
	cursor: pointer;
}

form div {
	padding: 5px;
	display: -ms-flexbox;
	display: flex;
}


