.pb-logo {
    margin: 0 .6rem;
    color: black;
}

.pb-logo span {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    text-align: center;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    height: 2.4em;
    width: 2.4em;
    border-radius: 1.2em;
    border: 2px solid black;
    font-size: 1.5rem;
    background: -webkit-radial-gradient(white, red);
    background: -o-radial-gradient(white, red);
    background: radial-gradient(white, red);
}

.pb-navigation {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    padding: 1rem;
    width: 100%;
}

.pb-header {
    display: -ms-flexbox;
